/* width */
::-webkit-scrollbar {
    width: 9px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #a6a6a6;
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #636363; 
    border-radius: 10px;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #545454; 
  }

/* Skill Bar */
/* Make sure that padding behaves as expected */


/* Container for skill bars */
.container {
  width: 100%; /* Full width */
  height: 5px;
  background-color: #ddd; /* Grey background */
}

.skills {
  text-align: right; /* Right-align text */
  color: white; /* White text color */
}

circle,
rect,
line {
  stroke-width: 10px;
  stroke-linecap: round;
  fill: transparent;
}

.ai {width: 75%; height: 5px; background-color: #30D1FA;}
.frontend {width: 60%; height: 5px; background-color: #30D1FA;}
.data {width: 50%; height: 5px; background-color: #30D1FA;}
